import React from "react";
import { BsGithub, BsFacebook } from "react-icons/bs";
import { FaDiscord, FaBars, FaTimes } from "react-icons/fa";
const Navbar = () => {
  const sidebar = () => {
    document.querySelector(".fixed").style.width = "100vw";
  };
  const close = () => {
    document.querySelector(".fixed").style.width = "0vw";
  };
  return (
    <>
      <div className=" fixed z-[1] h-screen w-0 top-0 right-0 bg-black duration-300 flex justify-center items-center">
        <FaTimes
          className="mb-[30rem] translate-x-[25rem] text-4xl cursor-pointer"
          onClick={close}
        />
        <ul className="text-2xl w-full">
          <a onClick={close} href="#home">
            <li className="py-2 duration-200 w-full text-center hover:bg-green-500 hover:text-black">
              Home
            </li>
          </a>
          <a onClick={close} href="#skills">
            <li className="py-2 duration-200 w-full text-center hover:bg-green-500 hover:text-black">
              Skills
            </li>
          </a>
          <a onClick={close} href="#about">
            <li className="py-2 duration-200 w-full text-center hover:bg-green-500 hover:text-black">
              About
            </li>
          </a>
          <a onClick={close} href="#contact">
            <li className="py-2 duration-200 w-full text-center hover:bg-green-500 hover:text-black">
              Contact us
            </li>
          </a>
        </ul>
      </div>
      <div>
        <div className="flex px-5 justify-between w-full h-[50px] bg-[#34495e] items-center fixed left-0 right-0 ">
          <p
            className="text-2xl italic uppercase text-white"
            style={{ fontFamily: "'Rubik Puddles', cursive" }}
          >
            Kaonain
          </p>
          <ul className="hidden gap-4 sm:flex">
            <li className="active:text-green-600 hover:text-green-600">
              <a href="#home">Home</a>
            </li>
            <li className="active:text-green-600 hover:text-green-600">
              <a href="#skills">Skills</a>
            </li>
            <li className="active:text-green-600 hover:text-green-600">
              <a href="#about">About</a>
            </li>
            <li className="active:text-green-600 hover:text-green-600">
              <a href="#contact">Contact us</a>
            </li>
          </ul>
          <FaBars className="h-40 sm:hidden cursor-pointer" onClick={sidebar} />
        </div>

        <div className="flex justify-end">
          <a href="http://github.com/kaonainlabib">
            <div className="fixed top-[30%] left-0 flex flex-col">
              <div className="text-3xl flex -ml-[7rem] gap-3 px-4 py-3 bg-gray-800 hover:-ml-[0.5rem] rounded-lg duration-300">
                Github
                <BsGithub />
              </div>
            </div>
          </a>
          <a href="http://facebook.com">
            <div className="fixed top-[42%] left-0 flex flex-col">
              <div className="text-3xl flex -ml-[9.8rem] gap-3 px-4 py-3 bg-blue-700 hover:-ml-[0.5rem] rounded-lg duration-300">
                Facebook
                <BsFacebook />
              </div>
            </div>
          </a>
          <a href="https://discord.gg/rNDvdZWrjt">
            <div className="fixed top-[calc(42%+12%)] left-0 flex flex-col">
              <div className="text-3xl flex -ml-[7.9rem] gap-3 px-4 py-3 bg-violet-800 hover:-ml-[0.5rem] rounded-lg duration-300">
                Discord
                <FaDiscord />
              </div>
            </div>
          </a>
        </div>
      </div>
    </>
  );
};

export default Navbar;
