import React, { useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

const Skills = () => {
  gsap.registerPlugin(ScrollTrigger);

  useEffect(() => {
    gsap.to(".html", {
      width: "96%",
      duration: 2,
      delay: 0,
      scrollTrigger: {
        trigger: ".html",
      },
    });
  }, []);
  useEffect(() => {
    gsap.to(".css", {
      width: "90%",
      duration: 2,
      delay: 0,
      scrollTrigger: {
        trigger: ".css",
      },
    });
  }, []);
  useEffect(() => {
    gsap.to(".js", {
      width: "85%",
      duration: 2,
      delay: 0,
      scrollTrigger: {
        trigger: ".js",
      },
    });
  }, []);
  useEffect(() => {
    gsap.to(".tailwind", {
      width: "80%",
      duration: 2,
      delay: 0,
      scrollTrigger: {
        trigger: ".tailwind",
      },
    });
  }, []);
  useEffect(() => {
    gsap.to(".bootstrap", {
      width: "71%",
      duration: 2,
      delay: 0,
      scrollTrigger: {
        trigger: ".bootstrap",
      },
    });
  }, []);
  useEffect(() => {
    gsap.to(".react", {
      width: "40%",
      duration: 2,
      delay: 0,
      scrollTrigger: {
        trigger: ".react",
      },
    });
  }, []);

  return (
    <div className="w-full h-screen scroll-mt-14" id="skills">
      <p className="max-w-[1000px] text-4xl text-center font-bold mx-auto border-[#27ae60] border-b-4 w-[7ch] selection:bg-[#e74c3c] " >
        My Skills
      </p>
      <div className="pt-10">
        <div className="mx-auto flex justify-between mb-1 font-semibold text-blue-600 text-lg w-3/4">
          <div className="flex">HTML</div>
          <span>96%</span>
        </div>
        <div className="mx-auto w-3/4 bg-gray-200 rounded-md h-3">
          <div
            className="bg-blue-600 h-3 rounded-md delay html"
            style={{ width: "0%" }}
          ></div>
        </div>
        <div className="pt-10">
          <div className="mx-auto flex justify-between mb-1 font-semibold text-purple-500 text-lg w-3/4">
            <div className="flex">CSS</div>
            <span>90%</span>
          </div>
          <div className="mx-auto w-3/4 bg-gray-200 rounded-md h-3">
            <div
              className="bg-purple-500 h-3 rounded-md delay css"
              style={{ width: "0%" }}
            ></div>
          </div>
        </div>
        <div className="pt-10">
          <div className="mx-auto flex justify-between mb-1 font-semibold text-red-700 text-lg w-3/4">
            <div className="flex">Javascript</div>
            <span>85%</span>
          </div>
          <div className="mx-auto w-3/4 bg-gray-200 rounded-md h-3">
            <div
              className="bg-red-700 h-3 rounded-md delay js"
              style={{ width: "0%" }}
            ></div>
          </div>
        </div>
        <div className="pt-10">
          <div className="mx-auto flex justify-between mb-1 font-semibold text-teal-500 text-lg w-3/4">
            <div className="flex">Tailwind CSS</div>
            <span>80%</span>
          </div>
          <div className="mx-auto w-3/4 bg-gray-200 rounded-md h-3">
            <div
              className="bg-teal-500 h-3 rounded-md delay tailwind"
              style={{ width: "0%" }}
            ></div>
          </div>
        </div>
        <div className="pt-10">
          <div className="mx-auto flex justify-between mb-1 font-semibold text-amber-500 text-lg w-3/4">
            <div className="flex">Bootstrap</div>
            <span>71%</span>
          </div>
          <div className="mx-auto w-3/4 bg-gray-200 rounded-md h-3">
            <div
              className="bg-amber-500 h-3 rounded-md delay bootstrap"
              style={{ width: "0%" }}
            ></div>
          </div>
        </div>
        <div className="pt-10">
          <div className="mx-auto flex justify-between mb-1 font-semibold text-rose-500 text-lg w-3/4">
            <div className="flex">React JS (Learning...)</div>
            <span>40%</span>
          </div>
          <div className="mx-auto w-3/4 bg-gray-200 rounded-md h-3">
            <div
              className="bg-rose-500 h-3 rounded-md delay react"
              style={{ width: "0%" }}
            ></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Skills;
