function textAnimate(...textArr) {
    let textClass = document.querySelector(".text");
    let textLetter = 0;
    let textArrStr = 0;
    let delay = 100;
    

    function write() {
        
        if (textLetter < textArr[textArrStr].length) {
            textClass.textContent += textArr[textArrStr].charAt(textLetter);
            textLetter++;
            document.querySelector('.cursor').classList.remove("anime")
            setTimeout(write, delay);
        } else {
            setTimeout(backSpace, 200);
            document.querySelector('.cursor').classList.add("anime")

        }
    }
    write();



    function backSpace() {
        if (0 < textLetter) {
            textClass.textContent = textArr[textArrStr].substring(0, textLetter - 1);
            textLetter-- ;
            document.querySelector('.cursor').classList.remove("anime")
            setTimeout(backSpace, 90);
        } else {
            textArrStr++;
            if (textArrStr >= textArr.length) {
                textArrStr = 0;
            document.querySelector('.cursor').classList.add("anime")

            }
            setTimeout(write, 500);
        }
    }
}
export default textAnimate;