import React from "react";
import { HiArrowNarrowRight } from "react-icons/hi";
const Home = () => {
  return (
    <div
      id="home"
      className="p-[5rem] pt-[8rem] sm:p-[10rem] sm:pt-[10rem] w-full h-screen flex justify-start max-w-[1000px] "
    >
      <div className="">
        <p className="text-sm font-bold justify-self-start">Hello! I am</p>
        <span className="text-4xl font-extrabold text-indigo-500 selection:bg-green-600 text h-[2.5rem]"></span>
        <span className="text-4xl w-[0.5rem] bg-green-600 border-2 border-green-600 anime cursor"></span>
        <p className="my-3 first-letter:text-2xl ">
          A front-end Developer, with experience of more than 1 year. Currently
          learning NodeJS. Interested at making modern UI/UX Websites...{" "}
        </p>
        <div>
          <a href="#skills">
            <div className="cursor-pointer flex gap-3 px-5 border w-[15ch] py-3 group hover:bg-[#27ae60] duration-300">
              My Skills{" "}
              <HiArrowNarrowRight className="text-2xl inline duration-300 group-hover:rotate-90" />
            </div>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Home;
