import React,{useState} from "react";

const Contact = () => {
  const [text, settext] = useState("");
const handleChange = (event) => { 
  settext(event.target.value);
 }


  return (
    <form id="contact" className="pt-40 px-20 sm:px-40 md:px-60 lg:px-80 scroll-mt-14 " action="https://api.slapform.com/3nbXjJTUd" method="POST">
      <p className="text-center text-3xl py-8">Fill the Form</p>
      <div className="flex flex-col">
        <div className="relative z-0 mb-6 w-full group">
          <input
            type="text"
            name="floating_first_name"
            id="floating_first_name"
            className="block py-2.5 px-0 w-full text-sm  bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-green-600 focus:outline-none focus:ring-0 focus:border-green-600 peer"
            placeholder=" "
            required
            />
          <label
            htmlFor="floating_first_name"
            className="absolute text-sm text-gray-100 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-green-600 peer-focus:dark:text-green-600 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:-translate-y-6"
            >
            First name
          </label>
        </div>
        <div className="relative z-0 mb-6 w-full group">
          <input
            type="text"
            name="floating_last_name"
            id="floating_last_name"
            className="block py-2.5 px-0 w-full text-sm  bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-green-600 focus:outline-none focus:ring-0 focus:border-green-600 peer"
            placeholder=" "
            required
            />
          <label
            htmlFor="floating_last_name"
            className="absolute text-sm text-gray-100 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-green-600 peer-focus:dark:text-green-600 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:-translate-y-6"
            >
            Last name
          </label>
        </div>
        <div className="relative z-0 mb-6 w-full group">
          <textarea
            name="textarea"
            id="textarea"
            className="mt-3 h-[20ch] resize-none block py-2.5 px-0 w-full text-sm  bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-green-600 focus:outline-none focus:ring-0 focus:border-green-600 peer"
            maxLength="300"
            required
            onChange={handleChange}
            />
          <label
            htmlFor="floating_last_name"
            className="absolute text-lg text-gray-100 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-green-600 peer-focus:dark:text-green-600 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:-translate-y-6"
          >
            Your Queries
          </label>
          <span>{text.length}/300</span>
        </div>
      </div>
      
      <button
        type="submit"
        className="text-white bg-green-600 hover:bg-green-700 focus:ring-2 focus:outline-none font-medium rounded-[0.2rem] text-md w-full sm:w-auto px-5 py-2.5 text-center"
      >
        Submit
      </button>
    </form>
  );
};

export default Contact;
