import React from "react";
import swal from "sweetalert";
import "./skills_style.css";


const About = () => {
  const copy = () => {
    navigator.clipboard.writeText("jn2021ctg@gmail.com");
    swal({
      title: "Email Address Copied to the clipboard",
      icon: "success",
      button: "Okay",
    });
  };
  return (
    <div className="w-full h-[60vh]  ">
      <p
        className="scroll-mt-14 max-w-[800px] text-4xl text-center font-bold mx-auto border-[#27ae60] border-b-4 w-[8ch] selection:bg-[#e74c3c] "
        id="about"
      >
        About Me
      </p>
      <div className="flex py-8 flex-col px-[5rem] sm:px-[10rem] pt-10 ">
        <p className="text-lg sm:text-xl leading-normal first-letter:text-2xl first-letter:text-indigo-500">
          Hello, I am Kaonain Labib Nisus. I am a front-end Developer with an
          experience of more than 1 year. Currently learning NodeJS. Interested
          at making modern UI/UX Websites...
        </p>
        <code className="text-lg sm:text-xl py-8">
          If you wanna contact with me, please fill the <a href="#form">form</a>{" "}
          given below or shoot me an{" "}
          <span className="cursor-pointer text-emerald-400" onClick={copy}>
            E-mail
          </span>
          .
        </code>
      </div>

    </div>
  );
};

export default About;
